import React from "react";
import LppCardsCarousel from "../components/LppCardsCarousel";

export default function Home() {
  return (
    <div className="home-landing">
      <h1 className="home-title">Bienvenido a LPP-Curso</h1>
      <p className="home-desc">
        Plataforma interactiva para aprender y practicar pseudocdigo LPP.<br />
        Accede a temas, galera, creadores y mucho ms tras iniciar sesin.
      </p>
      {/* Puedes agregar aqu un botn llamativo para ir a login o registro */}

      <section className="lpp-intro-section" style={{ marginTop: 48 }}>
        <h2 style={{
          fontSize: '2rem',
          fontWeight: 700,
          color: 'var(--azul-claro, #004aad)',
          marginBottom: 24,
          textAlign: 'center',
          letterSpacing: '0.01em'
        }}>
          LPP? Cmo empiezo con esto?
        </h2>
        <LppCardsCarousel />
      </section>
    </div>
  );
}

/*
El validador de cdigo se ha removido de la Home y solo estar en la ruta /validador.
*/
