import React, { useState } from "react";
import "./LppCardsCarousel.css";

const cards = [
  {
    title: "Qu es LPP y por qu aprenderlo?",
    alt: "Qu es LPP?",
    text: `El Lenguaje de Programacin para Principiantes (LPP) es una herramienta pedaggica diseada para introducir a estudiantes especialmente en colegios y primeros semestres universitarios al pensamiento lgico y computacional. No se trata de un lenguaje de programacin profesional como Python o Java, sino de un pseudocdigo estructurado que sigue reglas sencillas y claras. Esto permite que los alumnos se concentren en comprender la lgica de los algoritmos sin distraerse con la complejidad de la sintaxis de lenguajes avanzados.\n\nEn otras palabras, LPP funciona como un puente que facilita el paso del pensamiento natural al pensamiento algortmico.`
  },
  {
    title: "Cmo puedo empezar con LPP?",
    alt: "Cmo empezar con LPP?",
    text: `Para iniciarse en LPP, no se requiere experiencia previa en programacin. El camino recomendado es:\n\n Familiarizarse con la sintaxis bsica: comandos simples como Escribir, Leer, SiEntonces, Mientras o Para que representan las estructuras ms comunes en cualquier lenguaje real.\n Resolver pequeos problemas cotidianos: por ejemplo, disear un algoritmo que calcule el promedio de tres nmeros, que muestre si un nmero es par o impar, o que imprima una tabla de multiplicar.\n Usar plataformas interactivas (como LPP-Curso): que validan el cdigo, corrigen errores y permiten practicar en un entorno amigable.\n Dar el salto progresivo a lenguajes formales: una vez adquirida la lgica, la transicin a C, Python o Java se vuelve mucho ms natural.`
  },
  {
    title: "Cul es el objetivo central de LPP?",
    alt: "Objetivo de LPP",
    text: `El objetivo central de LPP es formar en pensamiento algortmico y resolucin de problemas, ms que ensear un lenguaje en s mismo.\n\nCon LPP se busca que los estudiantes:\n Aprendan a descomponer un problema grande en pasos pequeos.\n Reconozcan las estructuras de control fundamentales (condicionales, ciclos, secuencias).\n Desarrollen habilidades de anlisis lgico que podrn aplicar luego en cualquier disciplina tecnolgica.\n\nEn este sentido, LPP se convierte en una herramienta para entrenar la mente en procesos ordenados y sistemticos, una competencia clave en la era digital.`
  },
  {
    title: "Qu recursos ofrece LPP para el aprendizaje?",
    img: "/assets/lpp-recursos.png",
    alt: "Recursos de LPP",
    text: `LPP aporta varios elementos que enriquecen el proceso educativo:\n\n Simplicidad sintctica: sus instrucciones son fciles de leer, casi como frases en espaol, lo que reduce la barrera de entrada.\n Ambiente controlado de prctica: los errores son comprensibles y estn pensados para guiar al aprendiz, no para frustrarlo.\n Variedad de ejercicios progresivos: desde ejemplos bsicos hasta problemas ms complejos que retan al estudiante a pensar de manera abstracta.\n Validadores y entornos grficos: como LPP-Curso, que ofrecen retroalimentacin inmediata, ejercicios guiados, galeras de ejemplos y mdulos para trabajar en comunidad.\n Transversalidad educativa: LPP no se limita a informtica; se puede aplicar en matemticas, fsica, lgica o resolucin de problemas cotidianos.\n\nEn resumen, LPP no busca crear programadores expertos de inmediato, sino despertar en los estudiantes el hbito de pensar como un programador: analtico, lgico y orientado a soluciones.`
  }
];

export default function LppCardsCarousel() {
  const [active, setActive] = useState(0);

  const prevCard = () => setActive((prev) => (prev === 0 ? cards.length - 1 : prev - 1));
  const nextCard = () => setActive((prev) => (prev === cards.length - 1 ? 0 : prev + 1));

  return (
    <div className="lpp-carousel-container">
      <button className="lpp-carousel-arrow left" onClick={prevCard} aria-label="Anterior">&#8592;</button>
      <div className="lpp-carousel-track">
        {cards.map((card, idx) => {
          let className = "lpp-carousel-card";
          if (idx === active) className += " active";
          else if (idx === (active - 1 + cards.length) % cards.length) className += " prev";
          else if (idx === (active + 1) % cards.length) className += " next";
          else className += " hidden";
          return (
            <div className={className} key={card.title}>
              <div className="lpp-card-content">
                <h3>{card.title}</h3>
                <p>{card.text.split('\n').map((line, i) => <span key={i}>{line}<br /></span>)}</p>
              </div>
            </div>
          );
        })}
      </div>
      <button className="lpp-carousel-arrow right" onClick={nextCard} aria-label="Siguiente">&#8594;</button>
    </div>
  );
}
